.PopUpBackground{
	position: fixed; 
	width: 100%;
	height: 100%;
	background-color: rgba(40,40,40,0.70);
	display: block;
}

.PopUpContent{
	position: fixed; 
	width: 79vw;
	height: calc(100% - 12vw);
	background-color:white;
	display: block;
	bottom: 8.5vw;
	left:3vw;
	border: solid 0.5vw #0F1259;
	border-radius: 1vw;
}

.PopUpContent::after{
	content: "";
	position: absolute;
	left: 2vw;
	bottom: 0;
	margin-bottom:-3vw;
	margin-left: -0.46vw;
	width: 0;
    height: 0;
    border-left: 3.5vw solid transparent;
    border-right: 3.5vw solid transparent;
    border-top: 2.5vw solid #0F1259;
}
.PopUpContent::before{
	content: "";
	position: absolute;
	left: 2vw;
	bottom: 0.6vw;
	margin-bottom:-3vw;
	margin-left: -0.46vw;
    border-left: 3.5vw solid transparent;
    border-right: 3.5vw solid transparent;
    border-top: 2.5vw solid white;
    z-index: 10000;
}

